.form{
    display: flex;
    justify-content: center;
    margin: 80px 50px;
    gap: 20px;
    form{
        max-width: 80%;
        background-color: #f7f7f7;
        box-shadow: 7px 7px 15px rgba(0, 0, 0, 0.08);
        padding: 8px 100px;
        flex: 1;
    }

    h2{
        font-size: 32px;
        font-weight: 400;
        color: #212121;
    }
}
