.campo{
    margin: 24px 0;
    font-family: 'Montserrat', sans-serif;
    
    label{
        font-size: 18px;
        font-weight: 600;
        display: block;
        margin-bottom: 8px;
    }

    input{
        width: 100%;
        background-color: #ffffff;
        box-shadow: 10px 10px 30px rgba(0,0,0,0.08);
        border: none;
        padding: 30px 20px;
        outline-color: #6278f7;
        font-size: 16px;
        box-sizing: border-box;
    }
}

.campo_color{
    input {
        height: 40px;
        padding: 5px 20px;
    }
} 