.option__list{
    label{
        display: block;
        font-size: 18px;
        font-weight: 600;
        padding: 8px 0;
        font-family: 'Montserrat', sans-serif;
    }

    select{
        width: 100%;
        background-color: white;
        box-shadow: 10px 10px 30px rgba(0,0,0,0.06);
        padding: 24px;
        border: none;
        outline-color: #6278f7;
        font-size: 16px;
    }
}