.members{
    width: 280px;
    margin-bottom: 24px;
    position: relative;
    
    .delete{
        position: absolute;
        right: -15px;
        top: -15px;
        cursor: pointer;
    }

    .head{
        background-color: #f0f0f0;
        border-radius: 10px 10px 0 0;

        img{
            width: 100px;
            border-radius: 50%;
            position: relative;
            bottom: -50px;
        }
    }

    .info{
        background-color: #ffffff;
        box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.08);
        border-radius: 0 0 10px 10px;
        padding-top: 90px;
        padding-bottom: 40px;
        min-height: 100px;
        font-family: 'Montserrat', sans-serif;

        h4{
            color: #6278f7;
            font-size: 18px;
            line-height: 22px;
            font-weight: bold;
            margin-bottom: 8px;
        }

        h5{
            font-size: 18px;
            line-height: 22px;
            color: #212121;
            padding: 0 24px;
            font-weight: 400;
            margin: 0; 
        }
    }
}